import { request } from 'utils';

request.prefix = '/api/categories';

export const getListPaperType = (page = 1, IsPagingEnabled = true) =>
  request({
    url: '/PaperType',
    params: {
      page,
      IsPagingEnabled,
    },
    method: 'GET',
  });
export const getPaperType = id =>
  request({
    url: `/PaperType/${id}`,
    method: 'GET',
  });
export const deletePaperType = id =>
  request({
    url: `/PaperType/${id}`,
    method: 'DELETE',
  });
export const updatePaperType = (id, values) =>
  request({
    url: `/PaperType/${id}`,
    data: {
      id: values.id,
      label: values.code,
      labelInEnglish: values.labelInEnglish || '',
      typeId: values.typeId,
    },
    method: 'PUT',
  });

export const createPaperType = values =>
  request({
    url: '/PaperType',
    data: {
      id: values.id,
      label: values.code,
      labelInEnglish: values.labelInEnglish || '',
      typeId: values.typeId,
    },
    method: 'POST',
  });



